@import url(https://fonts.googleapis.com/css2?family=Raleway:wght@300&display=swap);
@import url(https://fonts.googleapis.com/css2?family=Dancing+Script:wght@300&display=swap);
@import url(https://fonts.googleapis.com/css2?family=Dancing+Script:wght@300&family=Indie+Flower&display=swap);
html {
	height: 100%;
	width: 100%;
	scroll-behavior: smooth;
}

body {
	height: 100%;
	margin: 0px;
	width: 100%;
	scroll-behavior: smooth;
	overflow-x: hidden;
}
#root {
	width: 100%;
	height: 100%;
	scroll-behavior: smooth;
}
a:link,
a:visited,
a:hover,
a:active {
	text-decoration: none;
}

.background {
	width: 100%;
	height: 100%;
	background-color: rgba(162, 229, 255, 0.933);
}
.loading-container {
	width: 100%;
	position: absolute;
	top: 50%;
	transform: translateY(-50%);
}
.loading {
	background-color: white;
	box-shadow: 0 0 50px #fff, -10px 0 80px #f0f, 10px 0 80px #0ff;
	width: 50%;
	height: 100%;
	text-align: center;
	font-size: 60px;
	margin: auto;
}
.loading div {
	height: 10%;
	margin: 10px;
}
.loading img {
	position: relative;
	height: auto;
	max-height: 100px;
	width: auto;
	max-width: 33%;
}
@media (max-width: 600px) {
	.loading {
		font-size: 40px;
	}
}
@media (max-width: 300px) {
	.loading {
		width: 100%;
		font-size: 16px;
	}
}

.container {
	position: relative;
	width: 85%;
	padding-top: 2%;
	padding-bottom: 2%;
	padding-left: 7.5%;
	padding-right: 7.5%;
	height: 96%;
}
.row {
	display: table;
	table-layout: fixed;
}
.inner-Component {
	width: 100%;
	height: 100%;
}
.logos {
	height: 100%;
	position: relative;
	font-size: 0px;
	margin: auto;
}
.section-title {
	width: 100%;
	height: 10%;
	position: relative;
	background-color: #a970ff;
	font-size: 4em;
	text-align: center;
}

.card-description-text {
	padding-top: 3%;
	padding-left: 2%;
	padding-right: 2%;
	padding-bottom: 3%;
}
.main-card {
	display: table;
	position: relative;
	margin: auto;
	top: 10%;
	height: 80%;
	width: 80%;
	bottom: 10%;
	border: 1px solid black;
	box-shadow: 0 0 50px #fff, -10px 0 80px #f0f, 10px 0 80px #0ff;
	overflow: hidden;
}
.main-card-img {
	border-right: 1px solid black;
	display: table-cell;
	position: relative;
	overflow: hidden;
	height: 100%;
	width: 55%;
}

.card-img {
	height: 100%;
	width: auto;
	position: absolute;
	top: 0;
	left: 50%;
	transform: translateX(-50%);
}
.card-description {
	background-color: white;
	display: table-cell;
	position: absolute;
	width: 45%;
	font-size: 30px;
	height: 100%;
	overflow-y: scroll;
}

@media (max-width: 800px), (max-height: 800px) {
	.card-description {
		font-size: 20px;
	}
}
@media (max-width: 600px), (max-height: 600px) {
	.card-description {
		font-size: 12px;
	}
}
@media (max-width: 400px), (max-height: 400px) {
	.card-description {
		font-size: 8px;
	}
}

#app {
	width: 100%;
	height: 100%;
}
#app [data-font='Raleway'],
[data-font='Raleway'] {
	font-family: 'Raleway', sans-serif;
}

#app [data-font='Dancing'],
[data-font='Dancing'] {
	font-family: 'Dancing Script', cursive;
}
#app [data-font='Indie'],
[data-font='Indie'] {
	font-family: 'Indie Flower', cursive;
}

.section {
	height: 100%;
	width: 100%;
}
.intro-section {
	background-color: black;
	align-content: center;
	overflow: hidden;
}
#Welcome {
	position: relative;
	margin: auto;
	margin-bottom: 3%;
	width: 60%;
	text-align: center;
	border: 1px solid rgb(90, 40, 255);
}
#Top-Star {
	background-color: white;
	align-content: center;
	border-top: 1px solid black;
	border-bottom: 1px solid black;
	box-sizing: border-box;
}
#Top-Star[data-darkMode='true'] {
	background-color: black;
}

#intro-video {
	position: absolute;
	z-index: 0;
	top: 0px;
	left: 0px;
	width: 100%;
	height: 100%;
	-webkit-filter: grayscale(60%); /* Safari 6.0 - 9.0 */
	filter: grayscale(60%);
}

#to-top-star {
	border: 10px solid rgb(90, 40, 255);
	border-radius: 20px;
	margin: auto;
	position: relative;
	top: 70%;
	width: 50%;
	color: rgb(90, 40, 255);
	box-shadow: inset 0 0 50px #fff, inset 20px 0 80px #f0f,
		inset -20px 0 80px #0ff, inset 20px 0 300px #f0f, inset -20px 0 300px #0ff,
		0 0 50px #fff, -10px 0 80px #f0f, 10px 0 80px #0ff;
	text-align: center;
	z-index: 1;
	font-size: 4em;
	text-shadow: 0 0 1px #fff, 0 0 2px #fff, 0 0 6px #292736, 0 0 8px #292736,
		0 0 10px #292736, 0 0 12px #292736, 0 0 14px #292736;
	-webkit-animation: glow 1s infinite alternate, fadeIn ease-in 20s;
	        animation: glow 1s infinite alternate, fadeIn ease-in 20s;
}
@-webkit-keyframes fadeIn {
	0% {
		opacity: 0;
	}
	50% {
		opacity: 0;
	}
	100% {
		opacity: 1;
	}
}
@keyframes fadeIn {
	0% {
		opacity: 0;
	}
	50% {
		opacity: 0;
	}
	100% {
		opacity: 1;
	}
}
@-webkit-keyframes glow {
	to {
		text-shadow: 0 0 4px #fff, 0 0 8px #fff, 0 0 12px #d25ed4, 0 0 16px #d25ed4,
			0 0 20px #d25ed4, 0 0 24px #d25ed4, 0 0 28px #d25ed4;
	}
}
@keyframes glow {
	to {
		text-shadow: 0 0 4px #fff, 0 0 8px #fff, 0 0 12px #d25ed4, 0 0 16px #d25ed4,
			0 0 20px #d25ed4, 0 0 24px #d25ed4, 0 0 28px #d25ed4;
	}
}

@media (max-width: 1600px) {
	#to-top-star {
		font-size: 3em;
	}
}
@media (max-width: 1000px) {
	#Top-Star {
		height: 60%;
	}
	#to-top-star {
		font-size: 26px;
		border: 5px solid rgb(90, 40, 255);
	}
}
@media (max-width: 600px) {
	#Top-Star {
		height: 40%;
	}
	#to-top-star {
		font-size: 20px;
		width: 60%;
		border: 2px solid rgb(90, 40, 255);
	}
}
@media (max-width: 300px) {
	#to-top-star {
		width: 100%;
		font-size: 16px;
	}
}

#filter {
	font-size: 2em;
	background-color: rgb(90, 40, 255);
	height: auto;
	overflow: hidden;
}
#filter[data-darkMode='true'] {
	background-color: rgb(49, 0, 33);
}
#filter[data-filterOn='false'] {
	max-height: 0;
	transition: max-height 0.25s ease-out;
}
#filter[data-filterOn='false']:after {
	padding: 0px;
}
#filter[data-filterOn='true'] {
	height: auto;
	max-height: 1000px;
	transition: max-height 0.25s ease-in;
}
#filter .row {
	width: 100%;
	display: table;
	table-layout: fixed;
}
#filter .row .segment-6 {
	display: table-cell;
	position: relative;
	width: 50%;
}
#filter .row .segment-6 p {
	background-color: #0ff;
	margin-bottom: 5px;
	border: 1px solid black;
	padding-top: 5px;
	padding-bottom: 5px;
	margin-right: 10%;
	margin-left: 10%;
	width: 80%;
	text-align: center;
	overflow: hidden;
}
#filter .row .segment-6 div {
	position: relative;
	background-color: #d25ed4;
	text-align: center;
	padding-top: 5px;
	padding-bottom: 5px;
	margin-right: 10%;
	margin-left: 10%;
	width: 80%;
	border: 1px solid black;
	margin-bottom: 5px;
}

#filter .row .segment-6 .value {
	margin-left: 30%;
	width: 60%;
}

input[type='checkbox'] {
	font-size: 30px;
}

#filter .row .segment-6 div input {
	display: none;
}
#filter .row .segment-6 div label[data-checked='true'] {
	text-shadow: 0 0 4px #fff, 0 0 8px #fff, 0 0 12px #0ff, 0 0 16px #0ff,
		0 0 20px #0ff, 0 0 24px #0ff, 0 0 28px #0ff;
}

@media (max-width: 800px), (max-height: 500px) {
	#filter {
		font-size: 14px;
	}
}

#menu {
	height: 10%;
}
#menu[data-darkMode='true'] {
	background-color: black;
}
#menu a {
}
.row input {
	width: 0px;
	height: 0px;
	display: none;
}
.row label {
	-webkit-touch-callout: none;
	-webkit-user-select: none;
	-ms-user-select: none;
	user-select: none;
}
.header-segment-4 {
	overflow: hidden;
	display: table-cell;
	font-size: 3em;
	text-align: center;
	padding-top: 2%;
	padding-left: 5%;
	padding-bottom: 2%;
	padding-right: 5%;
	width: 30%;
}
.header-segment-4[data-darkMode='true'] {
	background-color: #9e9e9e;
	border: 1px solid white;
}
.header-segment-4[data-checked='false'] {
	border: 1px solid black;
}
.header-segment-4[data-checked='true'] {
	border: 1px solid black;
	box-shadow: inset 0 0 50px #fff, inset 20px 0 80px #f0f,
		inset -20px 0 80px #0ff, inset 20px 0 300px #f0f, inset -20px 0 300px #0ff,
		0 0 50px #fff, -10px 0 80px #f0f, 10px 0 80px #0ff;
}

#header {
	border: 1px solid black;
	box-shadow: inset 0 0 50px #fff, inset 20px 0 80px #f0f,
		inset -20px 0 80px #0ff, inset 20px 0 300px #f0f, inset -20px 0 300px #0ff,
		0 0 50px #fff, -10px 0 80px #f0f, 10px 0 80px #0ff;
	font-size: 4em;
	width: 40%;
}

@media (max-width: 1200px), (max-height: 1200px) {
	.header-segment-4 {
		padding: 10px;
		font-size: 40px;
	}
	#header {
		padding: 10px;
		font-size: 46px;
	}
}
@media (max-width: 800px), (max-height: 800px) {
	.header-segment-4 {
		padding: 10px;
		font-size: 20px;
	}
	#header {
		padding: 10px;
		font-size: 26px;
	}
}
@media (max-width: 550px), (max-height: 400px) {
	.header-segment-4 {
		font-size: 14px;
	}
	#header {
		font-size: 14px;
	}
}

